import {allocate, dinero, multiply, toUnit, up} from "dinero.js";
import {CHF} from "@dinero.js/currencies";
import {ScaledAmount} from "@dinero.js/core/dist/esm/types/ScaledAmount";

export interface BVGProps {
	income: number,
	age: number,
}

export const BVG = (props: BVGProps): JSX.Element => {
	const percentage = pillar2Percentage(props.age);
	const koordinierterLohn = computeKoordinierterLohn(props.income);
	const x = Math.round(10000 * (1 / 12));
	const koordinierterLohnProMonat = allocate(dinero({amount: koordinierterLohn, currency: CHF, scale: 0}), [x, 10000 - x])[0];
	const bvgBeitraege = multiply(koordinierterLohnProMonat, percentage);

	return (
		<tr>
			<td>BVG</td>
			<td></td>
			<td>{toUnit(bvgBeitraege, {digits: 2, round: up})}</td>
		</tr>
	);
}

function pillar2Percentage(age: number) : ScaledAmount<number> {
	switch (true) {
		case (age < 25):
			return {amount: 0, scale: 0};
		case (age < 35):
			return {amount :35, scale: 3};
		case (age < 45):
			return {amount: 5, scale: 2};
		case (age < 55):
			return {amount: 75, scale: 3};
		case (age >= 55):
			return {amount: 9, scale: 2};
		default:
			return {amount: 0, scale: 0};
	}
}

function computeKoordinierterLohn(income: number) {
	let koordinierterLohn = income - 25_095;
	if (koordinierterLohn <= 0) {
		return 0;
	}
	if (koordinierterLohn > 60_945) {
		return 60_945;
	}
	return koordinierterLohn;
}