import {allocate, dinero, multiply, toUnit, up} from "dinero.js";
import {CHF} from "@dinero.js/currencies";

export interface ALV1Props {
	income: number,
}

export const ALV1 = (props: ALV1Props): JSX.Element => {
	const x = Math.round(10000 * (1 / 12));
	const incomePerMonth = allocate(dinero({amount: props.income, currency: CHF, scale: 0}), [x, 10000 - x])[0];
	const alv1 = multiply(incomePerMonth, {amount: 11, scale: 3});
	return (
		<tr>
			<td>ALV1</td>
			<td>1.1%</td>
			<td>{toUnit(alv1, {digits: 2, round: up})}</td>
		</tr>
	)
}