import {useEffect, useState} from "react";
import { add, allocate, Dinero, dinero, greaterThanOrEqual, lessThan, toUnit, multiply, isZero, up} from 'dinero.js';
import {CHF} from '@dinero.js/currencies';
import { ScaledAmount } from '@dinero.js/core/dist/esm/types/ScaledAmount';

export interface QuellensteuerProps {
	income: number,
	canton: string,
	isMarried: boolean,
	countKids: number,
	isChurchMember: boolean,
}

export interface Steuerergebnis {
	betrag: Dinero<number>,
	prozent: ScaledAmount<number>,
}

export class Steuersatz {
	steuerbaresEinkommenLower: Dinero<number>;
	steuerbaresEinkommenUpper: Dinero<number>;
	steuersatzProzent: ScaledAmount<number>;
	steuernAbsolut: Dinero<number>;

	constructor(steuerbaresEinkommen: Dinero<number>, tarifschritt: Dinero<number>, steuersatzProzent: ScaledAmount<number>, steuernAbsolut: Dinero<number>) {
		this.steuerbaresEinkommenLower = steuerbaresEinkommen;
		this.steuerbaresEinkommenUpper = add(steuerbaresEinkommen, tarifschritt);
		this.steuersatzProzent = steuersatzProzent;
		this.steuernAbsolut = steuernAbsolut;
	}

	public isWithin(actualIncome: Dinero<number>) : boolean {
		return (greaterThanOrEqual(actualIncome, this.steuerbaresEinkommenLower) && lessThan(actualIncome, this.steuerbaresEinkommenUpper));
	}

	public getSteuer(income : Dinero<number>) : Dinero<number> {
		if(isZero(this.steuernAbsolut) && this.steuersatzProzent.amount === 0) {
			return dinero({amount: 0, currency: CHF})
		}

		if(isZero(this.steuernAbsolut)) {
			return multiply(income, this.steuersatzProzent);
		}

		if(this.steuersatzProzent.amount === 0) {
			return this.steuernAbsolut;
		}
		return multiply(income, this.steuersatzProzent);
	}

	public toString = () : string => {
		return `Steuersatz UntereGrenze: ${toUnit(this.steuerbaresEinkommenLower)} ObereGrenze: ${toUnit(this.steuerbaresEinkommenUpper)} SteuernAbsolut: ${toUnit(this.steuernAbsolut)} Prozent: ${this.steuersatzProzent.amount}`;
	}
}

export const Quellensteuer = (props: QuellensteuerProps): JSX.Element => {

	const [tax, setTax] = useState<Steuerergebnis>({betrag: dinero({amount: 0, currency: CHF}), prozent: {amount: 0, scale: 0}});

	// const { setAlert } = useContext(AlertContext);
	useEffect(() => {
		console.log("dooing request!");
		var path = "/taxdata/tar22" + props.canton.toLowerCase() + ".txt";

		const request = new XMLHttpRequest();
		request.open('GET', path);
		request.send();

		request.onload = () => {
			let strings : string[] = request.responseText.split("\n");

			console.log("lines " + strings.length);

			const tarifcode = buildTarifcode(props.isMarried, props.countKids, props.isChurchMember);
			let map = strings.map(line => matchLine(line, tarifcode));

			const x = Math.round(10000 * (1 / 12));
			let incomePerMonth = allocate(dinero({amount: props.income, currency: CHF, scale: 0}), [x, 10000 - x])[0];

			console.log(incomePerMonth.toJSON());

			let steuersatz = map.find(tarif => tarif?.isWithin(incomePerMonth));
			if (steuersatz) {
				setTax({betrag: steuersatz.getSteuer(incomePerMonth), prozent: steuersatz.steuersatzProzent});

				console.log("found steuersatz: " + steuersatz);
				console.log("steuern:" + toUnit(steuersatz.getSteuer(incomePerMonth)));
			} else {
				setTax({betrag: dinero({amount: 0, currency: CHF}), prozent: {amount: 0, scale: 0}})
			}

		};

		request.onerror = () => {
			console.log("request hat nicht geklappt!");
		}
	}, [props.isChurchMember, props.canton, props.income, props.countKids, props.isMarried]);

	return (
		<tr>
			<td>Quellensteuer</td>
			<td>{toUnit(multiply(dinero({amount: 100, scale: 0, currency: CHF }), tax.prozent), {digits: 2, round: up})}%</td>
			<td>{toUnit(tax.betrag, { digits: 2, round: up })}</td>
		</tr>
);
}

function buildTarifcode(isVerheiratet : boolean, countKids : number, isChurchMember : boolean) {
	var tarifcode = "";
	tarifcode = tarifcode + (isVerheiratet ? "B" : "A");
	tarifcode = tarifcode + countKids;
	tarifcode = tarifcode + (isChurchMember ? "Y" : "N");
	return tarifcode;
}

function matchLine(line: string, tarif: string) : Steuersatz | null {
	const regexp = new RegExp('^0601([A-Z]{2})(' + tarif + ')\\s{7}[0-9]{8}([0-9]{7})([0-9]{2})([0-9]{7})([0-9]{2})\\s([0-9]{2})([0-9]{7})([0-9]{2})([0-9]{3})([0-9]{2})\\s\\s\\s');

	let regExpExecArray = regexp.exec(line);
	if (regExpExecArray === null) {
		return null;
	}

	let canton = regExpExecArray[1];
	let tarifcode = regExpExecArray[2];
	let steuerbaresEinkommenFranken = regExpExecArray[3];
	let steuerbaresEinkommenRappen = regExpExecArray[4];
	let tarifschrittFranken =  regExpExecArray[5];
	let tarifschrittRappen = regExpExecArray[6];
	let countChildren = regExpExecArray[7];
	let steuernFranken = regExpExecArray[8];
	let steuernRappen = regExpExecArray[9];
	let prozentSteuern = regExpExecArray[10];
	let prozentSteuernKommastellen = regExpExecArray[11];


	const steuerbaresEinkommen = dinero({ amount: +(steuerbaresEinkommenFranken + steuerbaresEinkommenRappen), currency: CHF });
	const tarifschritt = dinero({ amount: +(tarifschrittFranken + tarifschrittRappen), currency: CHF})
	const steuernAbsolut = dinero({ amount: +(steuernFranken + steuernRappen), currency: CHF});
	const steuersatz = {amount: +(prozentSteuern + prozentSteuernKommastellen), scale: 4} as ScaledAmount<number>;

	return new Steuersatz(steuerbaresEinkommen, tarifschritt, steuersatz, steuernAbsolut);

	// console.log(steuerbaresEinkommen.toFormat('$0,0.00', undefined));
	// console.log(tarifschritt.toFormat('$0,0.00', undefined));
	// console.log(steuernAbsolut.toFormat('$0,0.00', undefined));
	// console.log(steuersatz.toFormat('$0,0.00', undefined));

	//
	// console.log("match!");
	// console.log("canton: " + canton);
	// console.log("tarifcode " + tarifcode);
	// console.log("steuerbaresEinkommen: " + steuerbaresEinkommenFranken + "." + steuerbaresEinkommenRappen);
	// console.log("tarifschritt: " + tarifschrittFranken + "." + tarifschrittRappen);
	// console.log("anzahlKinder: " + countChildren);
	// console.log("steuern: " + steuernFranken + "." + steuernRappen);
	// console.log("steuernProzent: " + prozentSteuern + "." + prozentSteuernKommastellen);
}


